.cart-page{
    width: 95%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    gap:20px;
}

.selected-products{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.cart-page .cart-products-head{
    display: grid;
    grid-template-columns: 320px 180px 180px 140px;
    grid-template-rows: 100px;
}

.selected-products img{
    max-width: 80px;
    max-height: 80px;
}

.selected-products .selected-product{
    display: grid;
    grid-template-columns: 40px repeat(5,200px) ;
    grid-template-rows: 100%;
    margin: 20px 0px;
    padding: 8px;
    border-radius: 8px;
    align-items: center;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
    -moz-box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
    box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
}

.selected-products .selected-product h2{
    color:blueviolet;
}

.selected-products .selected-product .price{
    color:rgb(0, 51, 75);
}

.selected-products .selected-product .delbtn img{

    max-width:25px;
    max-height: 25px;
    background-color: white;
    cursor: pointer;
}

.selected-products .selected-product .delbtn {
    background-color: white;

}



.product-quantity{
    display: flex;
    gap:9px;

    width: fit-content;
    padding: 0px 16px;
    border-radius: 30px;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
    -moz-box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
    box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
}

.selected-products .selected-product .product-quantity button{
    background-color: white;

    cursor: pointer;

    font-size: 20px;

}


.cart-details{
    display: flex;
    flex-direction: column;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
    -moz-box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
    box-shadow: 1px 1px 5px 0px rgba(160, 159, 159, 0.75);
    align-items: center;
    padding: 16px;
    border-radius: 30px;
    height: fit-content;
}

.cart-details h2{
    color:rgb(189, 28, 189);
    border-bottom: 1px solid rgb(177, 177, 177);
}

.cart-details p{
    font-size: 1.4em;
    color:rgb(189, 28, 189);
}

.cart-details button{
    padding: 12px 16px;
    color:white;
    font-size: 16px;
    border-radius: 8px;
    background-color: blueviolet;
}

.gif{
    width: 20%;
    margin: 0 auto;
}