.home-head{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 60px;
}

.search-btn button{
    padding: 10px 16px;
    font-size: 15px;
    border-radius: 8px;
    background-color: blueviolet;
    color:white;
}

.home-prodycts{
    padding: 8px;
}

.home-product-head{
    color:rgb(39, 119, 211);
    
}

.home-product-head p{
    font-size: 24px;
    font-weight: 700;
}

.home-product{
    display: grid;
    grid-template-columns: repeat(6,200px);
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 28px 0px;
}

.home-product img{
    max-width: 60px;
    max-height: 60px;
}

.home-product button{

    padding: 18px;
    color:white;
    border-radius: 8px;
    justify-self: center;
    width: fit-content;
}

.home-product input{
    font-size: 15px;
    padding: 12px 8px;
}